<template >

    <asideNavBar />
    <section class="right-side">
        <headerNav />
            <section class="main-content">
                
                <header>
                    <h2>Utilizadores do sistema</h2>
                </header>
                <section class="table-container" style="width: 100%;">
                    
                    <section class="options-crud">
                        <button @click="openPopUp(null,'Registrar')" class="criar-btn"> 
                            <Icon icon="mdi:register" />
                             <span>Registrar</span>
                            </button>
                    </section>

                    <table class="table-1">
                        <thead>
                            <tr>
                                <th>username</th>
                                <th>password</th>
                                <th>role</th>
                                <th>status</th>
                                <th>acoes</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index,keys) in users" :key="index" >
                                <td>{{ item.username ?? 'N/A' }}</td>
                                <td>{{ item.password ?? 'N/A' }}</td>
                                <td>{{ item.role_name ?? 'N/A' }}</td>
                                <td>{{ item.status ?? 'N/A' }}</td>
                                <td>
                                    <div class="btn-form-row">
                                        <button @click="openPopUp(item)" title="editar">
                                            <Icon  class="edit icon" icon="raphael:edit" />
                                        </button>
                                        <button title="eliminar" @click="deleteRegister(item.id)" >
                                            <Icon class="delete icon" icon="ic:round-delete" />
                                        </button>
                                    </div>
                                </td>
                            </tr>
                            <tr v-if="status === 208">
                                <td style="text-align: center;padding: 1em;opacity: .8;" colspan="8" > Sem registros </td>
                            </tr>
                        </tbody>
                    </table>
                </section>
            </section>
            <popup v-if="false" >
                <utilizadorForm :props-data="propsData"/>
            </popup>
    </section>
    
</template>

<script>
    import asideNavBar from '@/components/AsideNavBar.vue';
    import headerNav from '@/components/headerNav.vue';
    import popup from '@/components/popUp.vue'
    import axios from 'axios';
    import utilizadorForm from '@/components/utilizadorForm.vue' 
    import { KEY, BASE_URL } from '@/config';
    // import { mapState } from 'vuex';
    export default {
        components:{
            headerNav,
            asideNavBar,
            popup,
            utilizadorForm,
        },
        data(){
            return{
                status: 0,
                users:[],
                propsData: {title: '', data: {}, action: '' }
            }
        },

        methods:{
            openPopUp(item = {},action = 'editar' ) {
                if ( action === 'editar' ){
                    this.propsData.title = 'Editar Utilizador'
                    this.propsData.data = item
                    this.propsData.action = 'editar'
                }else{
                    this.propsData.title = action+' Utilizador'
                    this.propsData.data = {}
                    this.propsData.action = action
                    
                }

                // this.$store.state.popUp = {...this.$store.state.popUp, show:true, title: 'Editar utilizador' }
            },



            async getAllUtilizador(){
                
                try {
                    const res = await axios.get(`${BASE_URL}/utilizador/?key=${KEY}`)
                    this.users = res.data.data
                    this.status = res.status
                    // console.log('Dados de fun carregados');
                    
                } catch (error) {
                    console.log(error?.response ?? error);
                    this.$swal({
                        title: 'Error!',
                        text: 'Falha ao listar Utilizadores',
                        icon: 'error',
                        confirmButtonText: 'OK',
                        timer: 3000,
                        showCancelButton: false,
                        cancelButtonText: 'Cancelar',
                        reverseButtons: false
                    })
                }
                
               
            },
            
            deleteRegister(id){
                this.$swal({
                    title: 'Tem certeza?',
                    text: "Você não poderá reverter isso!",
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: 'Sim, deletar!',
                    cancelButtonText: 'Cancelar',
                }).then((result) => {
                    if (result.isConfirmed) {
                        try {
                            axios.delete(`${BASE_URL}/utilizador/${id}/deletar/?key=${KEY}`,this.formData)
                            .catch(err =>{
                                this.$swal.fire({
                                    position: 'top',
                                    icon: 'warning',
                                    title: 'Algo deu errado!',
                                    showConfirmButton: false,
                                    timer: 5000,
                                    toast: true
                                })
                            })

                            // this.$store.state.emiter = !this.$store.state.emiter
                            this.$swal({
                                title: 'Deletado!',
                                text: 'Seu registro foi deletado.',
                                icon: 'success',
                                confirmButtonText: 'OK',
                                timer: 3000,
                                showCancelButton: false,
                                cancelButtonText: 'Cancelar',
                                reverseButtons: false
                            })

                        } catch (error) {
                            this.$swal.fire({
                                position: 'top',
                                icon: 'warning',
                                title: 'Falha ao eliminar registro!',
                                showConfirmButton: false,
                                timer: 5000,
                                toast: true
                            })
                        }


                        
                    }
                })
            },

        },
        created(){
            this.getAllUtilizador()
            
        },
        computed:{
            // ...mapState(['emiter'])
        },

        watch:{
            // emiter(newValue, oldValue) {
            //     this.getAllUtilizador()
            // }
        }    
    }
</script>

